




























export default{
	data(){
		return{
			account:'',
			password:'',
			newPassword:""
		}
	},
	methods:{
	// 提交
		submit(){
			if (this.account.length < 5) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 5 个字符'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if(this.password != this.newPassword){
				uni.showToast({
					icon: 'none',
					title: '两次密码不一致'
				});
				return;
			}
			const data ={
				password:this.phone,
				account:this.account,
				newPassword:this.newPassword,
			}
			uni.request({
				url:'http://10.138.93.113:2221/ScreenTheWord/MainController.do',
				method:'POST',
				header: {
					'Access-Control-Allow-Origin':'*' 
				},
				data:{
					controllerRequestType:'loginControllerService',
					jsonString:{
						'requestType':"updateUserInfo",
						userInfo:data
					}	
				},
				
				success: (res) => {
					// if();
					uni.showToast({
						title: '修改成功'
					});
				},
				fail:(response) =>{
					
				}
			})
		}
	}
}



